<!-- second row ends -->

        </div>
      </div>



      <!-- Row 2: Status Overview & Project Progress -->
      <div class="col-12 mb-4">
        <div class="row ms-0 me-0">
         <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Grouped Attendance by Category</h5>
              </div>
              <div class="panel-body">
                <apexchart
                  v-if="attendanceOptions"
                  height="450"
                  type="bar"
                  :options="attendanceOptions"
                  :series="attendanceSeries"
                />
              </div>
            </div>
          </div>

          <!-- Overall Project Progress (Pie Chart) -->
          <div class="col-lg-6 col-md-12 mb-4">
            <div class="statbox panel box box-shadow h-100">
              <div class="panel-heading">
                <h5 class="text-center mb-0">Office Meetings By Due Date</h5>
              </div>
              <div class="panel-body">
                <div class="text-center">
                  <apexchart
                    v-if="meetingScheduleOptions"
                    height="450"
                    type="pie"
                    :options="meetingScheduleOptions"
                    :series="meetingSeries"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, inject,watch } from 'vue';
import '@/assets/sass/scrollspyNav.scss';
import apexchart from 'vue3-apexcharts';
import { useStore } from 'vuex';
import { useMeta } from '@/composables/use-meta';
import axios from 'axios';
import Multiselect from '@suadelabs/vue3-multiselect';
import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';

useMeta({ title: 'Human Resources' });
const formatNumber = inject('formatNumber');
const showMessage = inject('showMessage');
const showError = inject('showError');
const formatDate = inject('formatDate')
const printEmployees = inject('printEmployees')
const printLabourTypes = inject('printLabourTypes')
const printLabourRequests = inject('printLabourRequests')
const printApprovals = inject('printApprovals');
const printAllMeetings = inject('printAllMeetingsReport');
const store = useStore();

const printSupervisorsAttendance = inject('printRemoteAttendance')
const printSiteLabourAttendance = inject('printSiteLabourAttendance')


    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });



const date = ref(new Date().toISOString().slice(0, 10))



    watch(date, (newVal) => {
    if (newVal) {
       getHrSummary()
    }
});




// Overall Project Progress (Pie Chart)
const meetingScheduleOptions = computed(() => {
  return {
    chart: {
      type: 'pie',
      fontFamily: 'inherit'
    },
    labels: ['Scheduled Meetings', 'Due In 3 days','Past Meetings'],
    colors: ['#00883B', '#0d96d6','#756dc9'],
    legend: {
      position: 'bottom',
      fontSize: '16px',
      labels: {
        colors:  '#3b3f5c'
      }
    },
    dataLabels: {
      enabled: true,
      formatter: function(val) {
        return val.toFixed(1) + '%';
      },
      style: {
        fontSize: '16px',
        colors: ['#fff'] 
      }
    },
    tooltip: {
      theme: 'dark',
      style: {
        fontSize: '20px',
        colors: ['#fff'] 
      },
      y: {
        formatter: function(val) {
          return val ;
        }
      }
    },
    plotOptions: {
      pie: {
        donut: {
          size: '0%'
        },
        expandOnClick: true
      }
    },
    responsive: [{
      breakpoint: 480,
      options: {
        chart: {
          width: 200
        },
        legend: {
          position: 'bottom'
        }
      }
    }]
  };
});

// Budget Analysis: Approved vs. Consumed by Category (Grouped Bar Chart)
const attendanceOptions = computed(() => {
  const is_dark = store.state.is_dark_mode;
  return {
    chart: {
      type: 'bar',
      toolbar: { show: false },
      fontFamily: 'inherit',
      height: 350
    },
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: '55%',
        borderRadius: 4
      }
    },
    colors: ['#34c793', '#fc6a6a','#787375'],
    xaxis: {
      categories: ['Head Office', 'Site Supervisors', 'Site Labours'],
      labels: {
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '12px'
        }
      }
    },
    yaxis: {
      labels: {
        style: {
          colors: is_dark ? '#888ea8' : '#3b3f5c',
          fontSize: '12px'
        },
        formatter: function(val) {
          return val;
        }
      }
    },
    legend: {
      position: 'bottom',
      fontSize: '12px',
      labels: {
        colors: is_dark ? '#888ea8' : '#3b3f5c'
      }
    },
    tooltip: {
      theme: is_dark ? 'dark' : 'light',
      y: {
        formatter: function(val) {
          return val;
        }
      }
    },
    grid: {
      borderColor: is_dark ? '#191e3a' : '#e0e6ed',
      strokeDashArray: 3
    },
    dataLabels: {
      enabled: false
    }
  };
});

  const summary = ref('')
  const meetingSeries = ref([65, 35]); 


const attendanceSeries = ref([
  { name: 'Early Attendees', data: [] },
  { name: 'Late Attendees', data: [] },
  { name: 'Absentees', data: [] }
]);

let hrAttendance = null;  // temporary storage for site attendance

const getOfficeAttendances = async () => {
    try {
        const response = await axios.get(`https://ghrm.abooderp.com/api/employees/attendance/summary?company_id=6&date=${date.value}`)
        const headOffice = response.data
        summary.value.attendance.head_office = headOffice.early_count + headOffice.late_count
        if (hrAttendance) {
            attendanceSeries.value = [
                {
                    name: 'Early Attendees',
                    data: [
                        headOffice.early_count || 0,
                        hrAttendance.site_officers.early || 0,
                        hrAttendance.site_labours.early || 0
                    ]
                },
                {
                    name: 'Late Attendees',
                    data: [
                        headOffice.late_count || 0,
                        hrAttendance.site_officers.late || 0,
                        hrAttendance.site_labours.late || 0
                    ]
                },
                {
                    name: 'Absentees',
                    data: [
                        headOffice.absentees_count || 0,
                        0,
                        0
                    ]
                }
            ]
        }

    } catch (error) {
        console.log(error)
        showError(error.message)
    }
}

const getHrSummary = async () => {
    try {
        const response = await axiosInstance.get(`human-resources?date=${date.value}`)
        summary.value = response.data
        hrAttendance = summary.value.attendance

        meetingSeries.value = [
            summary.value?.meetings?.scheduled || 0,
            summary.value?.meetings?.upcoming || 0,
            summary.value?.meetings?.history || 0
        ]

        getOfficeAttendances()  // now we have head office + site attendance
    } catch (error) {
        console.log(error)
        showError(error.message)
    }
}


    const employees = ref([])
    const employees_count = ref({
      total : 0,
      male : 0,
      female : 0
    })
    const loading = ref(false)
    const getEmployees = () => {
    loading.value = true;
    axios.get('https://ghrm.abooderp.com/api/employees/information?entity_id=6').then((response) => {
            employees.value = response.data.data.map((item, index) => {
            const dob = new Date(item.date_of_birth);
            const today = new Date();
            let age = today.getFullYear() - dob.getFullYear();
            const m = today.getMonth() - dob.getMonth();
            if (m < 0 || (m === 0 && today.getDate() < dob.getDate())) {
                age--;
            }
            return {
                ...item,
                full_name : item.full_name.toUpperCase(),
                id: index + 1,
                age: age,
                department: item.department_name,
                 status: {
                    name: item.is_active === 'YES' ? 'Active' : 'Inactive',
                    color: item.is_active === 'YES' ? 'success' : 'dark'
                }            
            };
        });

            employees_count.value.total = employees.value.length;
            employees_count.value.male = employees.value.filter(e => e.gender === 'Male').length;
            employees_count.value.female = employees.value.filter(e => e.gender === 'Female').length;
        loading.value = false;
    });
};


const printFilteredEmployees = (gender)=>{
  const filteredEmployees = ref([])
  if(gender ==='all'){
        filteredEmployees.value = employees.value
  }else{
   filteredEmployees.value = employees.value.filter(e => e.gender === gender)
  }
   printEmployees(filteredEmployees.value,gender)
}

    onMounted(() => {
        getHrSummary();
        getEmployees()
    });

</script>

