  font-weight: 500;
}

/* Dark mode support */
:deep(.apexcharts-tooltip) {
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

:deep(.apexcharts-text) {
  font-family: inherit !important;
}

/* Ensure consistent chart heights */
:deep(.apexcharts-canvas) {
  margin: 0 auto;
}

/* Additional styles for horizontal bar charts */
:deep(.horizontal-bar-chart .apexcharts-bar-area) {
  transition: all 0.3s ease;
}

:deep(.horizontal-bar-chart .apexcharts-bar-area:hover) {
  filter: brightness(1.1);
}

/* Ensure proper spacing for long category names */
:deep(.apexcharts-yaxis-texts-g text) {
  dominant-baseline: middle;
}

/* Custom styling for horizontal bars */
:deep(.apexcharts-bar-series.apexcharts-plot-series .apexcharts-bar-area) {
  stroke-width: 0;
}

:deep(.apexcharts-bar-series.apexcharts-plot-series .apexcharts-bar-area:hover) {
  stroke-width: 1;
  stroke: #ffffff;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .col-lg-2, .col-md-4, .col-sm-6 {
    margin-bottom: 1rem;
  }
  
  h3 {
    font-size: 1.5rem;
  }
  
  h6 {
    font-size: 0.8rem;
  }
}

/* Add subtle animation to bars */
@keyframes barGrow {
  from {
    transform: scaleX(0);
    transform-origin: left;
  }
  to {
    transform: scaleX(1);
    transform-origin: left;
  }
}

:deep(.apexcharts-bar-area) {
  animation: barGrow 0.8s ease-out;
}
</style>
